@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  height: 50px;
  width: 100px;
  font-size: 20px;
}
body {
  display:flex;
  flex-direction: column;
  color: #ffffff;
}


.Navbar {
  display:flex;
  flex-direction: row;
  color: black;
}


.flower {
  display: flex;
  justify-content: center;
}

.download{
  display:flex;
  justify-content: center;
}


.colors {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}

.color-swatch {
  height: 70px;
  width: 70px;
  border-radius: 7px;
  margin: 9px;
  cursor: pointer;
  border: 4px solid black;
}

.color-swatch-active {
  box-shadow: inset 0 0 15px #000000;
  transition: all 0.2s;
}
:root {
  --light: #d0d1d3;
  --dark: #040d14;
  --green: #21ce99;
}

body {
  font-family: Poppins;
  background: #040d14;
  background: var(--dark);
  margin: 0;
}

h1 {
  color: #21ce99;
  color: var(--green);
}

.contactForm{
  display:flex;
  flex-direction: column;
}

form {
  margin-top: 0px;
  background: #040d14;
  background: var(--dark);
  display:flex;
  flex-direction: column;
}



.formRow {
  margin: 10px 0 20px 0;
}

.formInput {
  padding: 15px 10px;
  background-color: #040d14;
  background-color: var(--dark);
  box-shadow: 0 0 0 2px #21ce99;
  box-shadow: 0 0 0 2px var(--green);
  border: none;
  color: #21ce99;
  color: var(--green);
}

.formInput:focus {
  background-color: #040d14;
  background-color: var(--dark);
  outline: none;
  box-shadow: 0 0 0 2px #21ce99;
  box-shadow: 0 0 0 2px var(--green);
  color: #d0d1d3;
  color: var(--light);
}

.formInput:-ms-input-placeholder {
  color: #21ce99;
  color: var(--green);
  opacity: 1;
}

.formInput::placeholder {
  color: #21ce99;
  color: var(--green);
  opacity: 1;
}

.formInput:focus:-ms-input-placeholder {
  color: #d0d1d3;
  color: var(--light);
  opacity: 1;
}

.formInput:focus::placeholder {
  color: #d0d1d3;
  color: var(--light);
  opacity: 1;
}

.errorMessage {
  color: #f45532;
  font-size: 14px;
  margin: 0;
  padding: 0;
}

#notifyToast {
  color: #fff;
  border-radius: 6px;
  width: 200px;
  margin-left: 75px;
  background-color: #21ce99;
  background-color: var(--green);
}

.submit-btn {
  background-color: #040d14;
  background-color: var(--dark);
  color: #21ce99 !important;
  color: var(--green) !important;
  padding: 15px 50px;
  border: 2px solid #21ce99;
  border: 2px solid var(--green);
  border-radius: 10px;
  margin-bottom: 25px;
  
}

.submit-btn:hover {
  background-color: #21ce99;
  background-color: var(--green);
  color: #040d14 !important;
  color: var(--dark) !important;
  border: 2px solid #21ce99;
  border: 2px solid var(--green);
}

.submit-btn:disabled:hover {
  background-color: #040d14;
  background-color: var(--dark);
  color: #21ce99 !important;
  color: var(--green) !important;
  cursor: wait;
}

@media (max-width: 768px) {
  .submit-message {
    width: 125px;
    margin-left: 200px;
  }
}


